.PageVideoMobile {
  background: #FFFFFF; }
  .PageVideoMobile__wrapper {
    padding: 16px; }
  .PageVideoMobile__image-wrapper {
    margin-bottom: 10px; }
  .PageVideoMobile__item-image {
    width: 130px;
    height: 80px;
    border-radius: 6px;
    position: relative;
    margin-bottom: 16px; }
  .PageVideoMobile__duration {
    position: absolute;
    right: 5px;
    bottom: 5px;
    background: rgba(0, 0, 0, 0.5);
    border-radius: 3px;
    padding: 2px 4px 2px 4px; }
  .PageVideoMobile__duration-text {
    display: inline-block;
    font-size: 11px;
    color: #FFFFFF;
    opacity: 1; }
  .PageVideoMobile__item-title {
    padding-left: 12px;
    font-size: 15px;
    color: #000000;
    line-height: 20px;
    margin-bottom: 16px;
    max-height: 80px;
    overflow: hidden; }
  .PageVideoMobile a {
    text-decoration: none !important; }



