.GameListMobile table {
  margin-bottom: 8px; }

.GameListMobile__description {
  margin-top: -16px;
  font-size: 14px;
  line-height: 20px;
  padding-bottom: 19px; }

.GameListMobile__item {
  padding-bottom: 20px; }
  .GameListMobile__item:last-child {
    padding-bottom: 0; }

.GameListMobile__item-image-wrapper {
  position: relative;
  width: 80px;
  vertical-align: top; }

.GameListMobile__item-image {
  width: 80px;
  height: 80px;
  border-radius: 6px; }

.GameListMobile__discount-badge {
  width: 42px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  position: absolute;
  top: 4px;
  right: -6px;
  background: #E64646;
  border-radius: 6px;
  font-weight: 700;
  font-size: 13px;
  color: #FFFFFF;
  text-align: center;
  padding: 3px; }

.GameListMobile__item-info {
  padding-left: 12px;
  vertical-align: top; }

.GameListMobile__item-title {
  max-height: 50px;
  overflow: hidden; }
  .GameListMobile__item-title span {
    display: inline-block;
    font-size: 16px;
    line-height: 17px;
    padding-bottom: 1px; }

.GameListMobile__item-description {
  padding-bottom: 8px; }
  .GameListMobile__item-description span {
    font-size: 14px;
    color: #71757A;
    line-height: 18px;
    display: inline-block;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap; }

.GameListMobile__item-bottom {
  display: table;
  height: 30px;
  white-space: nowrap; }

.GameListMobile__item-price {
  font-weight: 700;
  font-size: 17px;
  color: #222222;
  display: table-cell;
  white-space: nowrap; }
  .GameListMobile__item-price--discounted {
    font-weight: 700;
    font-size: 15px;
    color: #FFFFFF;
    background: #E64646;
    border-radius: 100px;
    padding: 4px 8px;
    display: inline-block; }
    @media (max-width: 320px) {
      .GameListMobile__item-price--discounted {
        font-size: 14px; } }
  .GameListMobile__item-price--old {
    color: #909499;
    font-size: 14px;
    line-height: 21px;
    text-decoration: line-through;
    margin-left: 8px;
    display: inline-block; }
    @media (max-width: 320px) {
      .GameListMobile__item-price--old {
        font-size: 12px; } }

.GameListMobile button {
  padding: 6px 16px 7px 16px;
  text-align: center;
  margin: 0; }
  .GameListMobile button.small, .GameListMobile button.small:hover {
    background-image: url("./img/buy.svg") !important;
    background-position: center center !important;
    background-size: 18px 17px !important;
    background-repeat: no-repeat !important;
    height: 30px;
    width: 30px; }

.GameListMobile__controls {
  width: 100%;
  text-align: right;
  display: table-cell; }

.GameListMobile__buy-icon {
  background: url("./img/buy.svg") no-repeat center center;
  display: inline-block; }



