.CommunityListMobile__item:last-child .CommunityListMobile__item-image {
  margin-bottom: 0; }

.CommunityListMobile__item:last-child .CommunityListMobile__item-description {
  margin-bottom: 0; }

.CommunityListMobile__item-image {
  width: 48px;
  height: 48px;
  border-radius: 50%;
  margin-bottom: 16px; }

.CommunityListMobile__item-info {
  padding-left: 16px; }

.CommunityListMobile__item-title {
  line-height: 10px; }
  .CommunityListMobile__item-title span {
    font-size: 15px;
    line-height: 20px;
    font-weight: 500;
    display: inline-block;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap; }

.CommunityListMobile__item-description {
  line-height: 10px;
  margin-bottom: 16px; }
  .CommunityListMobile__item-description span {
    font-size: 14px;
    color: #71757A;
    line-height: 22px;
    display: inline-block;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap; }



