.PageAdditionMobile {
  background: #FFFFFF; }
  .PageAdditionMobile__wrapper {
    padding: 8px 10.5px; }
  .PageAdditionMobile table {
    width: 100%; }
  .PageAdditionMobile__item {
    padding: 8px 5.5px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    vertical-align: top; }
