.SliderMobile {
  position: relative; }
  .SliderMobile__list {
    overflow: auto;
    white-space: nowrap;
    padding-bottom: 20px; }
    .SliderMobile__list::-webkit-scrollbar {
      display: none; }
  .SliderMobile__item {
    display: inline-block;
    margin-right: 12px; }
    .SliderMobile__item:last-child {
      margin-right: 16px; }
  .SliderMobile__scroll-hide {
    width: 100%;
    position: absolute;
    left: 0;
    bottom: 0;
    height: 20px;
    background: #FFF;
    z-index: 1; }



